import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class BookOpenProgramService {

  private bookOpenProgramMatDrawer = new BehaviorSubject<any>(null);
  bokopenprogramdrawercast = this.bookOpenProgramMatDrawer.asObservable();

  private bookOpenProgramData = new BehaviorSubject<any>(null);
  bokopenprogramdatacast = this.bookOpenProgramData.asObservable();

  private bookOpenProgramDataSubmitted = new BehaviorSubject<any>(false);
  bokopenprogramdatasubmittedcast = this.bookOpenProgramDataSubmitted.asObservable();

  constructor() { }

  getBookOpenProgramMatDrawer(){
    return this.bookOpenProgramMatDrawer.value
  }

  setBookOpenProgramMatDrawer(value){
    this.bookOpenProgramMatDrawer.next(value);
  }

  getBookOpenProgramData(){
    return this.bookOpenProgramData.value
  }

  setBookOpenProgramData(value){
    this.bookOpenProgramData.next(value);
  }

  getBookOpenProgramDataSubmitted(){
    return this.bookOpenProgramDataSubmitted.value
  }

  setBookOpenProgramDataSubmitted(value){
    this.bookOpenProgramDataSubmitted.next(value);
  }

}
